.navbar {
  background: #1c2237;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
}

.container {
  z-index: 1;
  width: 100vw;
  max-width: 1300px;
  
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: default;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.logo-img {
  justify-self: start;
  cursor: default;
  width: 65px;
  height: 65px;
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.slogan-img {
  justify-self: start;
  cursor: pointer;
  width: 170px;
  height: 170px;
  padding-top: 10px;
  display: flex;
  align-items: center;
  margin-left: 25px;
  cursor: default;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}

.nav-item {
  height: 100px;
  border-bottom: 2px solid transparent;
  padding-top: 20px;
}

.nav-item:hover {
  border-bottom: 3px solid #f00946;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
  color: #fff;
}

/*---------- LARGE SCREEN ONLY -----------*/
@media screen and (min-width: 1200px) {
  .container {
    z-index: 1;
    width: 2000px;
    max-width: 2000px;
    margin-right: 20px;
    padding-right: 15px;
    padding-left: 0px;
  }

  .navbar-logo {
    margin-left: 5rem;
    font-size: 2.5rem;
  }
}
/*--------------------------------------*/

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .logo-img {
    margin-top: -60px;
  }

  .slogan-img {
    width: 120px;
    height: 120px;
    padding-top: 0;
    margin-top: -50px;
  }

  .nav-menu.active {
    background: #1c2237;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #f00946;
    transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 1.4rem;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar {
    width: 100vw;
  }

  .navbar-logo {
    font-size: 1.1rem;
    margin: 12px 5px 7px 5px
  }

  .logo-img {
    width: 45px;
    height: 45px;
    margin-top: -40px;
  }

  .slogan-img {
    margin-top: -35px;
    width: 85px;
    height: 85px;
    margin-left: 0;
  }

}

@media screen and (min-width: 961px) and (max-width: 1227px) {
  .navbar {
    width: 128%;
  }
}
:root {
  --primary: #fff;
}

.home-btn {
  padding: 10px 20px;
  border-radius: 4px;
  outline: none;
  border: 10px solid black;
  cursor: pointer;
  white-space: nowrap;
}

.btn--primary {
  color: #000;
  /* border: 1px solid var(--primary); */
  background-color: #0abcf9;
  background-image: linear-gradient(315deg, #0abcf9 0%, #2c69d1 74%);   
}

.btn--outline {
  background-color: #990000;
  background-image: linear-gradient(147deg, #990000 0%, #ff0000 74%);
  padding: 8px 20px;
  border: none;
  transition: all 0.3s ease-out;
  color: #000;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 10px 26px;
  font-size: 28px;
  font-weight: 600;
}


.btn--mobile {
  text-align: center;
  border-radius: 4px;
  width: 80%;
  text-decoration: none;
  font-size: 1.5rem;
  background-color: transparent;
  color: #fff;
  padding: 14px 20px;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}

.btn--wide {
  padding: 12px 64px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover,
.btn--mobile:hover {
  transition: all 0.3s ease-out;
  background-color: #ff7878;
background-image: linear-gradient(315deg, #ff7878 0%, #ff0000 74%);
  color: #000;
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
  box-shadow: 0 0 0 2em rgba(#fff,0);
  border:none;
}


.btn--wide:hover {
  color: #fff;
  background-color: #f00946;
  transition: all 0.2s ease-out;
  
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 8px 16px;
  height: 100%;
  width: 100%;
  margin-top: 15px;
}

.primary {
  background-color: #242424;
  color: #fff;
  border: none;
}

.btn--primary:hover {
  background-color: #05e8ba;
  background-image: linear-gradient(315deg, #05e8ba 0%, #087ee1 74%);
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
  box-shadow: 0 0 0 2em rgba(#fff,0);
  color: #242424;
  border: none;
  transform: scale(1.2);
}

@-webkit-keyframes pulse  {
  0% {
    box-shadow: 0 0 0 0px rgba(255,255,255,0.2);
    transform: scale(1);
  }

  70% {
    box-shadow: 0 0 0 35px rgba(255,255,255,0);
    transform: scale(1);
  }
}

@keyframes pulse  {
  0% {
    box-shadow: 0 0 0 0px rgba(255,255,255,0.2);
    transform: scale(1);
  }

  70% {
    box-shadow: 0 0 0 35px rgba(255,255,255,0);
    transform: scale(1);
  }
}










